import React, { Component } from 'react';
//import DoughnutChart from './Components/Charts/DoughnutChart';
import axios from 'axios';
//import HorizontalBarChart from './Components/Charts/HorizontalBarChart';
import NavBar from './Components/Layout/NavBar';
import { BrowserRouter } from 'react-router-dom';
import Routes from './Routes';

class App extends Component {

  state = {
    jsonData: [],
    places: [],
    positiveIN: [],
    positiveFN: [],
    cured: [],
    deaths: [],
    newsArticles: []
  }

  UNSAFE_componentWillMount(){
    let places = []
    let positiveIN = []
    let positiveFN = []
    let cured = []
    let deaths = []
    axios.get(`/covid-19/data_file.json`)
        .then( response => {
        let jsonData = response.data
        jsonData.forEach(element => {
            places.push(element.place)
            positiveIN.push(element.positiveIN)
            positiveFN.push(element.positiveFN)
            cured.push(element.cured)
            deaths.push(element.death)
        });
        this.setState({
          jsonData,
          places,
          positiveIN,
          positiveFN,
          cured,
          deaths
        })
    }).catch( err => console.log(err))

    axios.get('https://newsapi.org/v2/top-headlines?q=COVID&country=in&apiKey=dd2ed6706ad34d01b7f5d49a10a7cc11&sortBy=publishedAt')
    .then( response => {
      this.setState({
        newsArticles: response.data.articles
      })
    }).catch( err => console.log(err))
  }

  render() {
    return (
      <div>
          <BrowserRouter basename={`/covid-19`}>
            <Routes jsonData={this.state.jsonData}
                    places = {this.state.places} 
                    positiveIN = {this.state.positiveIN}
                    positiveFN = {this.state.positiveFN}
                    cured = {this.state.cured}
                    deaths = {this.state.deaths}
                    newsArticles = {this.state.newsArticles}      
            />
            <NavBar/>
          </BrowserRouter>
      </div>
    );
  }
}

export default App;
